
.router-slid-enter-active, .router-slid-leave-active {
  transition: all .4s;
}
.router-slid-enter, .router-slid-leave-active {
  -webkit-transform: translate3d(2rem, 0, 0);
          transform: translate3d(2rem, 0, 0);
  opacity: 0;
}
.page {
  height: 100%;
}
.container {
  position: relative;
  width: 90%;
  height: 90%;
  margin: 4% auto 30px;
  background: #295c73;
}
.start {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 100px;
  color: tomato;
}
.navigate {
  position: relative;
  width: 14%;
  height: 60px;
  margin: 0 auto;
}
.navigate button {
    position: absolute;
    padding: 0;
    background-color: transparent;
    border: 0;
    outline: 0;
    cursor: pointer;
    color: #e7ad52;
    -webkit-transform: scale(0.9999);
            transform: scale(0.9999);
    transition: color 0.2s ease, opacity 0.2s ease, -webkit-transform 0.2s ease;
    transition: color 0.2s ease, opacity 0.2s ease, transform 0.2s ease;
    transition: color 0.2s ease, opacity 0.2s ease, transform 0.2s ease, -webkit-transform 0.2s ease;
    pointer-events: auto;
    font-size: inherit;
}
.navigate .nav-l {
    left: 0;
}
.navigate .nav-r {
    right: 0;
}
.navigate .nav-r .controls-arrow {
      -webkit-transform: rotate(180deg);
              transform: rotate(180deg);
}
.controls-arrow {
  position: relative;
  width: 3.6em;
  height: 3.6em;
}
.controls-arrow::before, .controls-arrow::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 2.6em;
    height: 0.5em;
    border-radius: 0.25em;
    background-color: currentColor;
    transition: all 0.15s ease, background-color 0.8s ease;
    -webkit-transform-origin: 0.2em 50%;
            transform-origin: 0.2em 50%;
    will-change: transform;
}
.controls-arrow::before {
    -webkit-transform: translateX(0.5em) translateY(1.55em) rotate(45deg);
            transform: translateX(0.5em) translateY(1.55em) rotate(45deg);
}
.controls-arrow::after {
    -webkit-transform: translateX(0.5em) translateY(1.55em) rotate(-45deg);
            transform: translateX(0.5em) translateY(1.55em) rotate(-45deg);
}
html, body {
  height: 100%;
  background: #343334;
}
li {
  list-style: none;
}
.legend {
  width: 40%;
  position: absolute;
  top: 140%;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}
.legend img {
    width: 100%;
}
.drag-el {
  width: 100px;
  height: 100px;
  position: absolute;
  top: 100px;
  left: 100px;
  background: #fff;
  cursor: all-scroll;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  transition: box-shadow ease 0.3s;
}
.float {
  box-shadow: 0px 0px 18px 5px rgba(0, 0, 0, 0.3);
}



